import { createApp } from 'vue'
import App from './App.vue'
import './registerServiceWorker'
import router from './router'
import Swiper, { Navigation, EffectCoverflow, Pagination } from 'swiper/vue';
import 'swiper/css'
import Slider from "@/components/Slider";
import tool from "@/components/tool";

createApp(App).use(router).use(Swiper, {Navigation, EffectCoverflow, Pagination}).use(tool).use(Slider).mount('#app')
